<template>
    <div class="task pageSpace h100">
        <my-breadcrumb></my-breadcrumb>
        <div class="pageContent">
            <a-table :defaultExpandAllRows="true" :columns="columns" :data-source="tableData" rowKey="id" :pagination="false">
                <span slot="action" slot-scope="text,record">
                    <a class="t-optColor-normal" @click="pauseClick(record,-2)">暂停</a>
                    <a class="t-optColor-success" @click="pauseClick(record,10)">继续</a>
                </span>
            </a-table>
              <div style="float: right;margin-top: 10px">
                 <a-pagination v-model="current" :total="total" :pageSize="pageSize" @change="getCurrent(current)"></a-pagination>
             </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "fileRecordsDetail",
        data() {
            return {
                columns: [
                    {
                        dataIndex: "filename",
                        title: "文件名称"
                    },{
                        dataIndex: "fileSize",
                        title: "文件大小"
                    },{
                        dataIndex: "fileCode",
                        title: "文件编码"
                    },
                    {
                        title: '操作',
                        key: 'action',
                        scopedSlots: {customRender: 'action'}
                    },
                ],
                tableData: [],
                routerId: "", //页面跳转传递的id
                total:0, // 数据总条数
                current:1, // 当前页
                pageSize:10, // 每页条数
            }
        },
        methods: {
            // 接收详情传过来的参数
            getRouterPage() {
                this.routerId = this.$route.query.id;
            },
            // 获取表格数据
            getTableData() {
                let formData = new FormData();
                formData.append("dirId",this.routerId);
                formData.append("pageNum",this.current);
                formData.append("size",this.pageSize);
                this.$API.selectFilesByPage(formData).then(res => {
                    if (res.code === 1000) {
                        this.tableData = res.data.list;
                        this.total = res.data.total
                    } else {
                        this.tableData = [];
                        this.$message.warning('暂无数据')
                    }
                })
            },
            // 获取当前页码
            getCurrent(current){
                this.current = current;
                this.getTableData()
            },
            // 暂停
            pauseClick(column, flag) {
                if (flag === -2) {
                    column.status = -2;
                } else {
                    column.status = 10;
                }
                let format = new FormData();
                format.append("id",column.id);
                format.append("status",column.status);
                format.append("type",column.type);
                this.$API.editProject(format).then((res) => {
                    if (res.code === 1000) {
                        if (flag === -2) {
                            this.$message.success("执行任务已暂停");
                        } else {
                            this.$message.success("项目正在继续");
                        }
                    } else {
                        if (flag === -2) {
                            this.$message.warning("暂停项目失败");
                        } else {
                            this.$message.warning("继续执行任务失败");
                        }
                    }
                });
            },
        },
        mounted() {
            this.getRouterPage();
            this.getTableData()
        }
    }
</script>

